@import "~@/styles/base.scss";






























.top-menu {
  height: 50px;
  background-color: #141415;
  .user-avatar {
    height: 50px;
    display: flex;
    align-items: center;
    .avatar {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background: aliceblue;
      line-height: 40px;
      margin: 0 20px;
    }
    .user-name {
      color: lavender;
    }
  }
  .login {
    height: 50px;
    color: lavender;
    display: flex;
    align-items: center;
  }
}
