@import "~@/styles/base.scss";
















































.single-item {
  display: flex;
  justify-content: space-between;
  overflow: hidden;
  overflow-x: auto;
  .item-list {
    .image-box {
      width: 200px;
      height: 300px;
      margin: 10px;
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
    .ShowMe {
      display: flex;
      justify-content: space-between;
      width: 100%;
      button {
        border: none;
        background: dimgrey;
        font-size: 14px;
        margin: 0 5px;
        width: 100%;
      }
      .green {
        background: green;
        color: white;
      }
      .gray {
        background: dimgrey;
        color: white;
      }
    }
  }
}
::-webkit-scrollbar {
  width: 0; /* Remove scrollbar space */
  background: transparent; /* Optional: just make scrollbar invisible */
}
/* Optional: show position indicator in red */
::-webkit-scrollbar-thumb {
  background: transparent;
}
